.icon__awards {
  display: inline-block;
  width: 38px;
  height: 41px;
  background: url("../img/aboutUs/icon-award.png");
}

.icon__jobs {
  display: inline-block;
  width: 41px;
  height: 40px;
  background: url("../img/aboutUs/icon-checked.png");
}

.icon__happy {
  display: inline-block;
  width: 33px;
  height: 39px;
  background: url("../img/aboutUs/icon-dima.png");
}

.icon__reviews {
  display: inline-block;
  width: 41px;
  height: 37px;
  background: url("../img/aboutUs/icon-briefcase.png");
}

.section-header {
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
  font-family: "Amsdam";
}

.section-header__headline {
  color: #3e3d3d;
}

.section-header__headline--light {
  color: #ffffff;
}

.section-header__title {
  margin-top: 38px;
  margin-bottom: 58px;
  font-family: "CrimsonTextRoman";
  color: #3e3d3d;
}

.section-header__title--light {
  color: #ffffff;
}

.social {
  display: flex;
  justify-content: space-between;
  width: 60px;
  height: 12px;
}

.social-item__link--divider::before {
  content: '|';
}

.social-item__link--divider::after {
  content: '|';
}

.header {
  background: #ffffff;
  color: #3e3d3d;
}

.header-hero {
  background-image: url("../img/slider/Image.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

.main-nav {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 65px;
  padding-top: 15px;
  font-family: "Monserrat";
  font-size: 0.75em;
  font-weight: 400;
}

.main-nav__logo {
  display: inline-block;
  font-family: "Monserrat";
  font-size: 1.5em;
  font-weight: 400;
}

.main-nav__logo:visited {
  color: #757575;
}

.main-nav__logo-img {
  margin-right: 10px;
}

.main-nav__logo-text {
  display: inline-block;
  transform: translateY(-25%);
}

.main-nav__menu {
  display: none;
  max-width: 60%;
  color: #aaaaaa;
}

@media screen and (min-width: 62rem) {
  .main-nav__menu {
    display: inline-flex;
    flex: 0 0 60%;
  }
}

.main-nav__menu-item {
  margin-right: 16px;
}

.main-nav__menu-item:last-child {
  margin-right: 0;
}

.main-nav__menu-link {
  font-size: 1em;
  font-weight: 550;
  text-transform: uppercase;
  color: #aaaaaa;
}

.main-nav__menu-link:hover {
  color: #555557;
}

.header-hero {
  width: 100%;
  height: 635px;
  font-family: "Amsdam";
  color: #ffffff;
}

.header-hero__slider {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  width: 100%;
  height: 320px;
  margin-top: 180px;
  text-align: center;
}

.slider-box {
  max-width: 520px;
  align-self: center;
  justify-content: space-between;
  font-size: 1.071em;
  line-height: 7em;
}

.slider-box__sup-headline {
  font-size: 1em;
}

.slider-box__headline {
  text-transform: uppercase;
  font-size: 2.2em;
  border-top: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
}

.slider-box__button {
  position: relative;
  box-sizing: border-box;
  padding: 15px 30px;
  width: 155px;
  height: 50px;
  border: 2px solid #ffffff;
  color: #ffffff;
  text-transform: capitalize;
}

.slider-box__button::after {
  content: url(../img/slider/Arrow.png);
  position: absolute;
  top: 58.2%;
  left: 50%;
  margin-left: -7.5px;
}

.content_slider {
  width: 75%;
  margin: 0 auto;
  text-align: center;
  position: relative;
}

.content_slider .info__slide {
  width: 100%;
  position: absolute;
  left: 20%;
  opacity: 0;
  transition: opacity 1s;
}

.content_slider .info__slide.showed {
  opacity: 1;
}

.slider__swich {
  position: absolute;
  top: 50%;
  margin-top: -17.5px;
  display: block;
  width: 35px;
  height: 35px;
  background: #ffffff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
}

.slider__swich--right {
  right: 0;
}
